import React, { useState, useEffect } from 'react'

import './App.css';
import Comments from './js/components/Comments'
import Reply from './js/components/Reply'
import data from './data.json'

function App() {

  const [commentsData, setCommentsData] = useState(data);

  useEffect(() => {

  }, []);

  const handleAddComment = (comment) => {
    setCommentsData(prevState => ({
      ...prevState,
      comments: [...prevState.comments, comment]
    }));
    localStorage.setItem('commentsData', commentsData);
    console.log('comment', comment);
  }

  const handleAddReply = (comment) => {
    console.log('add reply', comment);
  }

  const handleCommentUpdate = (newComment) => {
    
    setCommentsData(prevState => ({
      ...prevState,
      comments: [...prevState.comments, newComment]
    }));
    localStorage.setItem('commentsData', commentsData);
    console.log('comment', comment);
  }

  const handleReset = () => {
    setCommentsData(data);

    localStorage.clear();
  }

  return (
    <div className="app">
      <div className="content">
        <div className="wrapper">
          {commentsData.comments ?
            <Comments
              currentUser={commentsData.currentUser}
              comments={commentsData.comments}
              onReply={e => handleAddReply(e)}
              onUpdate={e => handleCommentUpdate(e)}
            />
            : <em>No comments yet</em>}
          <Reply onSubmit={handleAddComment} type="send" currentUser={commentsData.currentUser} />
        </div>
      </div>
      <button onClick={handleReset}>Reset</button>
      <div className="attribution">
        Challenge by <a href="https://www.frontendmentor.io?ref=challenge" target="_blank" rel="noopener noreferrer">Frontend Mentor</a>. 
        Coded by <a href="https://twitter.com/mixchex" target="_blank" rel="noopener noreferrer">Mike Cheshire</a>.
      </div>
    </div>
  );
}

export default App;
