import React from 'react'
import Comment from './Comment'

const Comments = ({
    comments = [],
    currentUser,
    onReply,
    onUpdate
}) => {
    return (
        comments.length ? 
        <ul className="comments">
            {comments.map((comment, i) =>
                <li key={comment.id}>
                    <Comment
                        comment={comment}
                        currentUser={currentUser}
                        onReply={onReply}
                        onUpdate={onUpdate}
                    
                    />
                </li>
            )}
        </ul>: null
    )
}
export default Comments;